@use 'base/mixins' as m;
@use 'base/variables' as v;

.site-header{
    @include m.bgimage('../img/header.jpg', v.$secundarioOpacity);
    padding: 5rem 0;

    .logo{
        max-width: 20rem ;
        margin: 0 auto;
        @include m.tablet {
            margin: 0;
        }
    }
    .contenido-header{
        padding-top: 10rem;
        @include m.desktop{
            padding-top: 10rem;
        }

        h1{
            color: v.$blanco;
            font-size: 4.5rem;
            @include m.tablet {
                font-size: 5rem;
                max-width: 50%;
            }
            @include m.desktop{
                max-width: 40%;
            }
        }
    }
}